.main-section-text {
    /* border: 1px solid white; */
    position: relative;
    left: 20em;
    top: 20rem;
    width: 30vw;
    height: 50vh;
}

.main-section-text .text-button {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .7em;
    width: 18%;
    padding: .3em;
    background-color: white;
    color: rgba(22, 2, 138, 0.8);
    border-radius: 5px;
    font-weight: bolder;
