    font-family: 'Ubuntu', sans-serif;
    margin-bottom: .5rem;
}

.text-button .square {
    position: absolute;
    top: 1.5em;
    width: .4em;
    height: .4em;
    background-color: white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

