    color: white;
    font-weight: bold;
    font-size: .9em;
    margin: 2rem 0;
    line-height: 1.7em;
}

.main-section-text .buttons-grp {
    margin-top: 2.5em;
}

.buttons-grp .portfolio {
    padding: .9em 0;
