    font-size: .8em;
    color: rgb(55, 108, 253);
    border: none;
    outline: none;
    letter-spacing: .2em;
    font-weight: bolder;
    /* margin: 0 .4em; */
}

.buttons-grp .hire {
    padding: .9em 0;
    width: 12em;
    margin-right: 1.5em;
